<template>
  <div class="box bg-white">
    <v-btn v-for="indicator in indicators" :key="indicator.key" :color="getButtonColor(indicator.key)"
      :variant="selectedIndicators.includes(indicator.key) ? 'flat' : 'outlined'"
      @click="toggleIndicator(indicator.key)" class="ma-1" size="small">
      {{ indicator.label }}
    </v-btn>
    <!-- 錯誤狀態 -->
    <div v-if="errorState.hasError" class="error-state">
      <v-icon color="error" class="mb-2" size="48">mdi-alert-circle</v-icon>
      <div class="error-title">發生錯誤</div>
      <div class="error-message">{{ errorState.errorMessage }}</div>
    </div>
    <!-- 無數據狀態 -->
    <div v-else-if="showNoDataState" class="no-data-container">
      <div class="no-selection-state">
        <v-icon class="mb-3" color="primary" size="48">mdi-database-off</v-icon>
        <div> {{ selectedIndicators.length === 0 ? '請從上方選擇 1-2 個指標來顯示趨勢圖表' : '所選指標沒有可用數據' }}</div>
      </div>
    </div>

    <div ref="chartContainer" class="chart-container"></div>

  </div>
</template>

<script setup>
import * as echarts from 'echarts'

const chartContainer = ref(null)
const chartInstance = ref(null)

const responseTired = inject('responseTired', ref([]))
const responseBody = inject('responseBody', ref([]))
const responseNutrition = inject('responseNutrition', ref(null))

// 10個指標的配置陣列
const indicators = [
  // 身體組成指標 (6個) - 使用 responseBody
  { key: '體重', label: '體重', source: 'body', field: '體重', unit: 'kg' },
  { key: '體脂率', label: '體脂率', source: 'body', field: '體脂率', unit: '%' },
  { key: '肌肉量', label: '肌肉量', source: 'body', field: '肌肉量', unit: 'kg' },
  { key: '脂肪重', label: '脂肪重', source: 'body', field: '脂肪重', unit: 'kg' },
  { key: '水份比例', label: '水份比例(%)', source: 'body', field: '水份比例', unit: '%' },
  { key: '水份重', label: '水份重(kg)', source: 'body', field: '水份重', unit: 'kg' },

  // 營養指標 (1個)
  { key: '飲水量', label: '飲水量(ml)', source: 'nutrition', field: '飲水量', unit: 'ml' },

  // 血液指標 (3個) - 使用 responseTired
  { key: 'HB', label: '血紅蛋白(Hb)', source: 'tired', field: 'hb', unit: '' },
  { key: 'CK', label: '肌酸激酶(CK)', source: 'tired', field: 'ck', unit: '' },
  { key: 'BUN', label: '血中素氮(BUN)', source: 'tired', field: 'bun', unit: '' }
]

const selectedIndicators = ref(['體重'])

// 指標選擇邏輯
const toggleIndicator = (indicatorKey) => {
  const currentIndex = selectedIndicators.value.indexOf(indicatorKey)

  if (currentIndex > -1) {
    // 如果已選中，則取消選擇
    selectedIndicators.value.splice(currentIndex, 1)
  } else {
    // 如果未選中，檢查是否已達到最大選擇數量
    if (selectedIndicators.value.length < 2) {
      selectedIndicators.value.push(indicatorKey)
    }
    // 如果已選擇2個指標，阻止第三個指標的選擇（不執行任何操作）
  }
}

// 按鈕顏色處理函數
const getButtonColor = (indicatorKey) => {
  return selectedIndicators.value.includes(indicatorKey) ? 'primary' : 'default'
}

// 數據來源映射函數
const getIndicatorData = (indicator) => {
  try {
    let rawData = []
    let processedData = []

    switch (indicator.source) {
      case 'body':
        rawData = responseBody.value
        if (!rawData || !Array.isArray(rawData)) {
          console.warn(`身體數據不可用 (${indicator.key})`)
          return []
        }
        processedData = rawData
          .filter(item => item && typeof item === 'object')
          .map(item => {
            try {
              return {
                date: item.date,
                value: item[indicator.field]
              }
            } catch (mapError) {
              console.warn(`處理身體數據項目失敗:`, mapError)
              return null
            }
          })
          .filter(item =>
            item &&
            item.date &&
            item.value !== null &&
            item.value !== undefined &&
            !isNaN(parseFloat(item.value))
          )
        break

      case 'tired':
        rawData = responseTired.value
        if (!rawData || !Array.isArray(rawData)) {
          console.warn(`疲勞數據不可用 (${indicator.key})`)
          return []
        }
        processedData = rawData
          .filter(item => item && typeof item === 'object')
          .map(item => {
            try {
              return {
                date: item.date,
                value: item[indicator.field]
              }
            } catch (mapError) {
              console.warn(`處理疲勞數據項目失敗:`, mapError)
              return null
            }
          })
          .filter(item =>
            item &&
            item.date &&
            item.value !== null &&
            item.value !== undefined &&
            !isNaN(parseFloat(item.value))
          )
        break

      case 'nutrition':
        rawData = responseNutrition?.value.datas
        if (!rawData || !Array.isArray(rawData)) {
          console.warn(`營養數據不可用 (${indicator.key})`)
          return []
        }
        processedData = rawData
          .filter(item => item && typeof item === 'object')
          .map(item => {
            try {
              return {
                date: item.date,
                value: item[indicator.field]
              }
            } catch (mapError) {
              console.warn(`處理營養數據項目失敗:`, mapError)
              return null
            }
          })
          .filter(item =>
            item &&
            item.date &&
            item.value !== null &&
            item.value !== undefined &&
            !isNaN(parseFloat(item.value))
          )
        break

      default:
        console.error(`未知的數據來源: ${indicator.source}`)
        return []
    }

    return processedData
  } catch (error) {
    console.error(`獲取指標數據失敗 (${indicator.key}):`, error)
    // 在發生錯誤時，設置錯誤狀態但不阻止其他指標的處理
    if (selectedIndicators.value.includes(indicator.key)) {
      setError(`指標 "${indicator.label}" 數據處理失敗`)
    }
    return []
  }
}

// 日期對齊處理函數
const alignDataByDate = (data1, data2) => {
  try {
    // 驗證輸入數據
    if (!data1 || !Array.isArray(data1) || data1.length === 0) {
      console.warn('第一個數據集無效或為空')
      return {
        dates: [],
        values1: [],
        values2: []
      }
    }

    if (!data2 || !Array.isArray(data2) || data2.length === 0) {
      console.warn('第二個數據集無效或為空')
      return {
        dates: [],
        values1: [],
        values2: []
      }
    }

    // 提取並驗證日期
    const dates1 = new Set()
    const dates2 = new Set()

    data1.forEach(item => {
      if (item && item.date) {
        dates1.add(item.date)
      }
    })

    data2.forEach(item => {
      if (item && item.date) {
        dates2.add(item.date)
      }
    })

    // 找出共同日期
    const commonDates = [...dates1].filter(date => dates2.has(date)).sort()

    if (commonDates.length === 0) {
      console.warn('兩個數據集沒有共同的日期')
      setError('所選指標沒有共同的日期數據')
      return {
        dates: [],
        values1: [],
        values2: []
      }
    }

    // 建立日期到數值的映射
    const data1Map = new Map()
    const data2Map = new Map()

    data1.forEach(item => {
      if (item && item.date && item.value !== null && item.value !== undefined) {
        data1Map.set(item.date, parseFloat(item.value))
      }
    })

    data2.forEach(item => {
      if (item && item.date && item.value !== null && item.value !== undefined) {
        data2Map.set(item.date, parseFloat(item.value))
      }
    })

    // 對齊數據，確保每個共同日期都有對應的數值
    const alignedDates = []
    const values1 = []
    const values2 = []

    commonDates.forEach(date => {
      const val1 = data1Map.get(date)
      const val2 = data2Map.get(date)

      // 只有當兩個數據集都有該日期的有效數值時才加入
      if (val1 !== undefined && val2 !== undefined &&
        !isNaN(val1) && !isNaN(val2)) {
        alignedDates.push(date)
        values1.push(val1)
        values2.push(val2)
      }
    })

    return {
      dates: alignedDates,
      values1,
      values2
    }
  } catch (error) {
    console.error('日期對齊處理失敗:', error)
    return {
      dates: [],
      values1: [],
      values2: []
    }
  }
}

// 數據驗證函數
const validateData = (data) => {
  try {
    // 檢查數據是否存在且為陣列
    if (!data || !Array.isArray(data)) {
      return false
    }

    // 檢查陣列是否有內容
    if (data.length === 0) {
      return false
    }

    // 檢查每個數據項目是否有必要的屬性
    return data.every(item => {
      return item &&
        typeof item === 'object' &&
        item.date &&
        (item.value !== null && item.value !== undefined && !isNaN(item.value))
    })
  } catch (error) {
    console.error('數據驗證失敗:', error)
    return false
  }
}

// 計算屬性 - 數據有效性檢查
const hasValidData = computed(() => {
  try {
    if (selectedIndicators.value.length === 0) {
      return false
    }

    const validationResults = selectedIndicators.value.map(indicatorKey => {
      const indicator = indicators.find(ind => ind.key === indicatorKey)
      if (!indicator) {
        console.warn(`找不到指標配置: ${indicatorKey}`)
        return false
      }

      const data = getIndicatorData(indicator)
      const isValid = validateData(data)
      return isValid
    })

    const allValid = validationResults.every(result => result)
    return allValid
  } catch (error) {
    console.error('數據有效性檢查失敗:', error)
    return false
  }
})

// 計算屬性 - 是否顯示無數據狀態
const showNoDataState = computed(() => {
  // 如果有錯誤，不顯示無數據狀態
  if (errorState.value.hasError) return false

  // 如果沒有選擇指標，顯示選擇提示
  if (selectedIndicators.value.length === 0) return true

  // 如果選擇了指標但沒有有效數據，顯示無數據狀態
  if (selectedIndicators.value.length > 0 && !hasValidData.value) return true

  return false
})

const chartData = computed(() => {
  try {
    // 檢查數據有效性和選擇狀態
    if (!hasValidData.value || selectedIndicators.value.length === 0) {
      return null
    }

    if (selectedIndicators.value.length === 1) {
      // 單軸模式
      const indicatorKey = selectedIndicators.value[0]
      const indicator = indicators.find(ind => ind.key === indicatorKey)

      if (!indicator) {
        console.error(`找不到指標配置: ${indicatorKey}`)
        return null
      }

      const data = getIndicatorData(indicator)

      // 再次驗證處理後的數據
      if (!validateData(data)) {
        console.error(`指標 ${indicatorKey} 的數據處理後仍無效`)
        return null
      }

      return {
        mode: 'single',
        indicator,
        dates: data.map(item => item.date),
        values: data.map(item => parseFloat(item.value)),
        dataCount: data.length
      }
    } else if (selectedIndicators.value.length === 2) {
      // 雙軸模式
      const indicator1Key = selectedIndicators.value[0]
      const indicator2Key = selectedIndicators.value[1]
      const indicator1 = indicators.find(ind => ind.key === indicator1Key)
      const indicator2 = indicators.find(ind => ind.key === indicator2Key)

      if (!indicator1 || !indicator2) {
        console.error('找不到指標配置')
        return null
      }

      const data1 = getIndicatorData(indicator1)
      const data2 = getIndicatorData(indicator2)

      // 驗證兩個數據集
      if (!validateData(data1) || !validateData(data2)) {
        console.error('雙軸模式中有無效數據')
        return null
      }

      const alignedData = alignDataByDate(data1, data2)

      // 檢查對齊後的數據
      if (!alignedData.dates || alignedData.dates.length === 0) {
        console.warn('沒有共同的日期數據')
        return null
      }

      return {
        mode: 'dual',
        indicator1,
        indicator2,
        dates: alignedData.dates,
        values1: alignedData.values1.map(val => parseFloat(val)),
        values2: alignedData.values2.map(val => parseFloat(val)),
        dataCount: alignedData.dates.length,
        originalCount1: data1.length,
        originalCount2: data2.length
      }
    }

    return null
  } catch (error) {
    console.error('圖表數據處理失敗:', error)
    return null
  }
})

// 雙軸圖表配置函數
const getDualAxisOption = (data) => {
  const { indicator1, indicator2, dates, values1, values2 } = data

  return {
    color: CHART_COLORS,
    tooltip: {
      trigger: 'axis',
      axisPointer: {
        type: 'cross',
        crossStyle: {
          color: '#999'
        }
      },
      formatter: (params) => {
        let result = `${params[0].axisValue}<br/>`
        params.forEach((param, index) => {
          const indicator = index === 0 ? indicator1 : indicator2
          result += `${param.seriesName}: ${param.value} ${indicator.unit || ''}<br/>`
        })
        return result
      }
    },
    legend: {
      data: [indicator1.label, indicator2.label],
      top: 'top',
      left: 'center'
    },
    grid: {
      left: '8%',
      right: '8%',
      bottom: '30%',
      containLabel: false
    },
    xAxis: {
      type: 'category',
      data: dates,
      axisLabel: {
        rotate: 45,
        fontSize: 10
      }
    },
    yAxis: [
      {
        type: 'value',
        name: `${indicator1.label} (${indicator1.unit || ''})`,
        nameLocation: 'middle',
        nameGap: 35,
        position: 'left',
        nameTextStyle: {
          fontSize: 12,
          color: '#555'
        },
        axisLine: {
          lineStyle: {
            color: CHART_COLORS[0]
          }
        },
        axisLabel: {
          color: '#555'
        }
      },
      {
        type: 'value',
        name: `${indicator2.label} (${indicator2.unit || ''})`,
        nameLocation: 'middle',
        nameGap: 35,
        position: 'right',
        nameTextStyle: {
          fontSize: 12,
          color: '#555'
        },
        axisLine: {
          lineStyle: {
            color: CHART_COLORS[1]
          }
        },
        axisLabel: {
          color: '#555'
        }
      }
    ],
    series: [
      {
        name: indicator1.label,
        type: 'line',
        yAxisIndex: 0,
        data: values1,
        itemStyle: {
          color: CHART_COLORS[0]
        },
        lineStyle: {
          color: CHART_COLORS[0],
          width: 2
        },
        label: {
          show: true,
          position: 'top',
          fontSize: 10,
          color: CHART_COLORS[0],
          formatter: (params) => {
            return params.value
          }
        },
        symbol: 'circle',
        symbolSize: 6,
        emphasis: {
          itemStyle: {
            shadowBlur: 10,
            shadowOffsetX: 0,
            shadowColor: 'rgba(0, 0, 0, 0.5)'
          }
        }
      },
      {
        name: indicator2.label,
        type: 'line',
        yAxisIndex: 1,
        data: values2,
        itemStyle: {
          color: CHART_COLORS[1]
        },
        lineStyle: {
          color: CHART_COLORS[1],
          width: 2
        },
        label: {
          show: true,
          position: 'top',
          fontSize: 10,
          color: CHART_COLORS[1],
          formatter: (params) => {
            return params.value
          }
        },
        symbol: 'triangle',
        symbolSize: 6,
        emphasis: {
          itemStyle: {
            shadowBlur: 10,
            shadowOffsetX: 0,
            shadowColor: 'rgba(0, 0, 0, 0.5)'
          }
        }
      }
    ],
    dataZoom: [
      {
        type: 'slider',
        start: 0,
        end: 100,
        height: 20,
        bottom: 10,
        xAxisIndex: [0],
        brushSelect: false
      },
      {
        type: 'inside',
        start: 0,
        end: 100,
        xAxisIndex: [0],
        zoomOnMouseWheel: true,
        moveOnMouseMove: true
      }
    ]
  }
}

// 單軸圖表配置函數
const getSingleAxisOption = (data) => {
  const { indicator, dates, values } = data

  return {
    color: CHART_COLORS,
    tooltip: {
      trigger: 'axis',
      formatter: (params) => {
        const param = params[0]
        return `${param.axisValue}<br/>
                ${param.seriesName}: ${param.value} ${indicator.unit || ''}`
      }
    },
    legend: {
      data: [indicator.label],
      top: 'top',
      left: 'center'
    },
    grid: {
      left: '8%',
      right: '8%',
      bottom: '30%',
      containLabel: false
    },
    xAxis: {
      type: 'category',
      data: dates,
      axisLabel: {
        rotate: 45,
        fontSize: 10
      }
    },
    yAxis: {
      type: 'value',
      name: `${indicator.label} (${indicator.unit || ''})`,
      nameLocation: 'middle',
      nameGap: 35,
      nameTextStyle: {
        fontSize: 12
      }
    },
    series: [{
      name: indicator.label,
      type: 'line',
      data: values,
      itemStyle: {
        color: CHART_COLORS[0]
      },
      lineStyle: {
        color: CHART_COLORS[0],
        width: 2
      },
      label: {
        show: true,
        position: 'top',
        fontSize: 10,
        formatter: (params) => {
          return params.value
        }
      },
      symbol: 'circle',
      symbolSize: 6,
      emphasis: {
        itemStyle: {
          shadowBlur: 10,
          shadowOffsetX: 0,
          shadowColor: 'rgba(0, 0, 0, 0.5)'
        }
      }
    }],
    dataZoom: [
      {
        type: 'slider',
        start: 0,
        end: 100,
        height: 20,
        bottom: 10,
        xAxisIndex: [0],
        brushSelect: false
      },
      {
        type: 'inside',
        start: 0,
        end: 100,
        xAxisIndex: [0],
        zoomOnMouseWheel: true,
        moveOnMouseMove: true
      }
    ]
  }
}

const renderChart = () => {
  try {
    if (!chartInstance.value) {
      setError('圖表實例未初始化，請嘗試重新載入頁面')
      return
    }

    if (!chartData.value) {
      try {
        chartInstance.value.clear()
      } catch (clearError) {
        console.warn('清空圖表失敗:', clearError)
      }
      return
    }

    let option = null

    // 根據模式生成配置
    if (chartData.value.mode === 'single') {
      try {
        option = getSingleAxisOption(chartData.value)
      } catch (singleError) {
        setError(`單軸圖表配置生成失敗: ${singleError.message}`)
        return
      }
    } else if (chartData.value.mode === 'dual') {
      try {
        option = getDualAxisOption(chartData.value)
      } catch (dualError) {
        setError(`雙軸圖表配置生成失敗: ${dualError.message}`)
        return
      }
    } else {
      setError(`未知的圖表模式: ${chartData.value.mode}`)
      return
    }

    // 驗證配置並渲染
    if (option) {
      // 驗證配置的基本結構
      if (!option.series || !Array.isArray(option.series) || option.series.length === 0) {
        setError('圖表配置無效：缺少系列數據')
        return
      }

      const hasValidSeries = option.series.every(series =>
        series &&
        series.data &&
        Array.isArray(series.data) &&
        series.data.length > 0
      )

      if (!hasValidSeries) {
        setError('圖表數據無效：系列數據為空或格式錯誤')
        return
      }

      try {
        chartInstance.value.setOption(option, {
          notMerge: true,
          lazyUpdate: false,
          silent: false
        })
        setTimeout(() => {
          if (chartInstance.value) {
            chartInstance.value.resize()
          }
        }, 100)
        clearError()
      } catch (setOptionError) {
        setError(`圖表選項設置失敗: ${setOptionError.message}`)
      }
    } else {
      setError('無法生成圖表配置，請檢查數據格式')
    }
  } catch (error) {
    setError(`圖表渲染過程發生未預期錯誤: ${error.message}`)
  }
}

// ECharts 初始化函數
const initChart = () => {
  if (!chartContainer.value) {
    console.warn('圖表容器未找到')
    setError('圖表容器初始化失敗，請重新載入頁面')
    return
  }

  try {
    // 如果已存在實例，先銷毀
    if (chartInstance.value) {
      try {
        chartInstance.value.dispose()
      } catch (disposeError) {
        console.warn('銷毀舊圖表實例失敗:', disposeError)
      }
      chartInstance.value = null
    }

    // 檢查 ECharts 是否可用
    if (!echarts || typeof echarts.init !== 'function') {
      setError('ECharts 庫未正確載入，請重新載入頁面')
      return
    }

    chartInstance.value = echarts.init(chartContainer.value)

    // 添加錯誤處理監聽器
    chartInstance.value.on('error', (errorEvent) => {
      console.error('ECharts 運行時錯誤:', errorEvent)
      setError('圖表渲染過程中發生錯誤')
    })
    clearError()
  } catch (error) {
    console.error('ECharts 初始化失敗:', error)
    setError(`圖表初始化失敗: ${error.message}`)
  }
}

// 錯誤狀態管理
const errorState = ref({
  hasError: false,
  errorMessage: ''
})

const setError = (message) => {
  errorState.value = {
    hasError: true,
    errorMessage: message
  }
  console.error(`圖表錯誤 : ${message}`)
}

const clearError = () => {
  errorState.value = {
    hasError: false,
    errorMessage: ''
  }
}

watch(chartData, (newData, oldData) => {
  try {
    clearError()

    if (newData && chartInstance.value) {
      nextTick(() => {
        renderChart()
      })
    } else if (!newData && oldData) {
      if (chartInstance.value) {
        chartInstance.value.clear()
      }
    }
  } catch (error) {
    setError(`數據變化處理失敗: ${error.message}`)
  }
}, { deep: true })

watch(selectedIndicators, (newSelection, oldSelection) => {
  try {
    clearError()

    if (chartInstance.value) {
      nextTick(() => {
        if (newSelection.length === 0) {
          chartInstance.value.clear()
        } else {
          renderChart()
        }
      })
    }
  } catch (error) {
    setError(`指標選擇處理失敗: ${error.message}`)
  }
}, { deep: true })

watch([() => responseBody, () => responseTired, () => responseNutrition],
  (newVals, oldVals) => {
    try {
      clearError()
      nextTick(() => {
        if (!chartInstance.value && chartContainer.value) {
          initChart()
        }

        if (selectedIndicators.value.length > 0 && chartInstance.value) {
          renderChart()
        }
      })
    } catch (error) {
      setError(`原始數據變化處理失敗: ${error.message}`)
    }
  },
  { deep: true, immediate: true }
)


const handleResize = () => {
  if (chartInstance.value) {
    chartInstance.value.resize()
  }
}

onMounted(() => {
  nextTick(() => {
    if (chartContainer.value) {
      initChart()
      if (chartData.value) {
        renderChart()
      }
      window.addEventListener('resize', handleResize)
    } else {
      console.error('圖表容器未找到')
    }
  })
})

onUnmounted(() => {
  window.removeEventListener('resize', handleResize)

  if (chartInstance.value) {
    try {
      chartInstance.value.dispose()
      chartInstance.value = null
    } catch (error) {
      console.error('ECharts 實例銷毀失敗:', error)
    }
  }
})
</script>

