<template>
  <SearchBar @search="handleSearch">
    <template #icon>
      <v-icon>mdi-heart-pulse</v-icon>
    </template>
    <template #title>
      綜合監控
    </template>
    <template #dashboardTypes>
      <select class="select">
        <option>疲勞監控與身體組成分析</option>
      </select>
    </template>
  </SearchBar>

  <section class="pa-4" v-if="hasData">
    <!-- 上方兩個區塊 -->
    <v-row>
      <v-col cols="6">
        <div class="box bg-white">
          <RadarChart />
        </div>
      </v-col>
      <v-col cols="6">
        <div class="box bg-white">
          <h2>疲勞監控</h2>
          <tiredContainer />
        </div>
      </v-col>
    </v-row>

    <!-- 下方兩個區塊 -->
    <v-row>
      <v-col cols="6">
        <div class="box bg-white">
          <MixChart 
            :response-tired="responseTired"
            :response-body="responseBody"
            :response-nutrition="responseNutrition"
          />
        </div>
      </v-col>
      <v-col cols="6">
        <div class="box bg-white">
        </div>
      </v-col>
    </v-row>
  </section>
  <NoData v-else />
</template>

<script setup>
import SearchBar from '../components/searchBar.vue'
import NoData from '../components/noData.vue'
import RadarChart from '../psychology/components/radarChart.vue'
import tiredContainer from '../physiology/components/tiredContainer.vue'
import MixChart from './components/mixChart.vue'
import { dashboard } from '@/requests'

const selectedUserId = ref('user123')
const allDates = ref([])
const loading = ref(false)
const error = ref(null)
const queryNoDataText = ref('')

const responsePsychology = ref([])
const responseTired = ref([])
const responseBody = ref([])
const responseNutrition = ref(null)

const hasData = computed(() => {
  return responsePsychology?.value?.length > 0
})

const latestDatas = computed(() => {
  if (responsePsychology.value.length === 0) return {}

  // 取得最新日期的數據
  const sortedData = [...responsePsychology.value].sort((a, b) => new Date(b.date) - new Date(a.date))
  return sortedData[0] || {}
})

// 獲取心理數據
const fetchresponsePsychology = async (queryString) => {
  try {
    const result = await dashboard.getPsychologyIndicator(selectedUserId.value, queryString)
    if (result.data) {
      responsePsychology.value = result.data

      // 更新日期列表
      allDates.value = responsePsychology.value
        .map(data => data.date)
        .sort((a, b) => new Date(b) - new Date(a))
    } else {
      console.error('心理API 回傳資料格式錯誤:', result)
      responsePsychology.value = []
    }
  } catch (err) {
    console.error('fetchresponsePsychology失敗:', err)
    responsePsychology.value = []
  }
}

const fetchresponseTired = async (queryString) => {
  try {
    const tiredResponse = await dashboard.getPhysiologyIndicatorTired(selectedUserId.value, queryString)
    responseTired.value = tiredResponse.data || []
  } catch (err) {
    console.error('fetchresponseTired失敗:', err)
    responseTired.value = []
  }
}

const fetchresponseBody = async (queryString) => {
  try {
    const bodyResponse = await dashboard.getPhysiologyIndicatorBody(selectedUserId.value, queryString)
    responseBody.value = bodyResponse.data || []

  } catch (err) {
    console.error('fetchresponseBody失敗:', err)
    responseBody.value = []
  }
}

const fetchresponseNutrition = async (queryString) => {
  try {
    const response = await dashboard.getNutritionIndicatorWater(selectedUserId.value, queryString)
    responseNutrition.value = response.data
  } catch (err) {
    console.error('fetchresponseNutrition失敗:', err)
    responseNutrition.value = null
  }
}

// 統一獲取所有數據
const fetchAllData = async (searchParams) => {
  loading.value = true
  error.value = null

  try {
    const queryString = searchParams?.queryString || ''
    const userId = searchParams?.selectedUserId || selectedUserId.value

    selectedUserId.value = userId

    // 並行請求所有API
    await Promise.all([
      fetchresponsePsychology(queryString),
      fetchresponseBody(queryString),
      fetchresponseTired(queryString),
      fetchresponseNutrition(queryString)
    ])
  } catch (err) {
    error.value = '數據載入失敗，請稍後重試'
    console.error('數據載入錯誤:', err)
  } finally {
    loading.value = false
  }
}

// 處理搜索
const handleSearch = async (searchParams) => {
  await fetchAllData(searchParams)
}

provide('latestDatas', latestDatas)
provide('allDates', allDates)
provide('responsePsychology', responsePsychology)
provide('responseTired', responseTired)
provide('responseBody', responseBody)
provide('responseNutrition', responseNutrition)
provide('loading', loading)
provide('error', error)
provide('selectedUserId', selectedUserId)
</script>
